<template>
  <div class="seePage">
    <!-- 库存入库列表查看 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>报表系统</el-breadcrumb-item>
      <el-breadcrumb-item>财务报表</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">厂家往来明细表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看其他入库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="库存组织：" size="mini">
          <span>{{TableData.instoragList.company_name}}</span>
        </el-form-item>
        <el-form-item label="单据号：" size="mini">
          <p>{{TableData.instoragList.instorage_sn}}</p>
        </el-form-item>
        <el-form-item label="供应商(厂家)：" size="mini">
          <span>{{TableData.instoragList.supplier_name}}</span>
        </el-form-item>
        <el-form-item label="采购员：" size="mini">
          <span>{{TableData.instoragList.buyer_name}}</span>
        </el-form-item>
        <el-form-item label="仓库名称：" size="mini">
          <span>{{TableData.instoragList.warehouse_name}}</span>
        </el-form-item>
        <el-form-item label="所属部门：" size="mini">
          <span>{{TableData.instoragList.department_name}}</span>
        </el-form-item>
        <el-form-item label="厂家发货单号：" size="mini">
          <span>{{TableData.instoragList.factorysend_sn}}</span>
        </el-form-item>
        <el-form-item label="厂家发货日期：" size="mini">
          <span>{{TableData.instoragList.factorysend_time}}</span>
        </el-form-item>
        <el-form-item label="付款方式：" size="mini">
          <span>{{TableData.instoragList.receivables_mode_name}}</span>
        </el-form-item>
        <el-form-item label="入库类型：" size="mini">
          <span>{{TableData.instoragList.type_name}}</span>
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          <span>{{TableData.instoragList.business_time}}</span>
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          <span>{{TableData.instoragList.auditor_status_name}}</span>
        </el-form-item>
        <el-form-item label="分摊方式：" size="mini">
          <span>{{TableData.instoragList.other_price_type_name}}</span>
        </el-form-item>
        <el-form-item label="其他费用：" size="mini">
          <span>{{TableData.instoragList.other_price}}</span>
        </el-form-item>
        <el-form-item label="备注：" size="mini">
          <!-- <el-input size="small" type="text" v-model="TableData.instoragList.remark" suffix-icon="el-icon-edit-outline"
            :maxlength="300" :disabled="true" class="boxTextarea" style="width: 200px;"></el-input> -->
          <span>{{TableData.instoragList.remark}}</span>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goodsList" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="序号" type="index" width="60">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="入库数量" align="right" width="130">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="采购价" align="right" width="130">
          <template slot-scope="scope">
            {{scope.row.purchase_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" label="小计金额" align="right" width="130">
          <template slot-scope="scope">
            {{scope.row.subtotal_price|priceFormat}}
          </template>
        </el-table-column>
        <!-- <el-table-column  :show-overflow-tooltip="true" property="warehouse_name" label="仓库"  >
        </el-table-column> -->
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="other_price" label="其他费用" align="right" width="130">
        </el-table-column>
      </el-table>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <!-- 未审核 -->
      <template v-if="TableData.instoragList.auditor_status == 1">
        <el-button type="primary" @click="audit('otherStorageAuditorPassInStorage')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('otherStorageAuditorInvalidInStorage')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.instoragList.auditor_status == 3 || TableData.instoragList.auditor_status == 2 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportOtheninstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        instoragList: {},
        goodsList: []
      },
      chaid: false
    };
  },
  computed: { ...mapGetters(["factorydetrptOtheninstorageSeeGet"]) },
  methods: {
    ...mapActions(["factorydetrptIndexACT"]),
    goBack() {
      this.factorydetrptIndexACT({
        moduleName: "Index"
      });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          instorage_id: this.instorage_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg ? res.data.msg : res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 5) {
            sums[index] = parseFloat(sums[index]);
            return;
          } else if (index === 7) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
            return;
          } else if (index === 10) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
            return;
          } else {
            sums[index] = "";
            return;
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.instorage_id = this.factorydetrptOtheninstorageSeeGet.instorage_id;
    this.$axios({
      method: "get",
      url: this.$api.otherStorageInfoInStorage,
      params: {
        instorage_id: this.instorage_id
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.TableData.instoragList = res.data.data.instoragList;
        this.TableData.goodsList = res.data.data.goodsList;
      }
    });
  }
};
</script>

