<template>
  <div class="seePage">
    <!-- 采购入库列表查看 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>报表系统</el-breadcrumb-item>
      <el-breadcrumb-item>财务报表</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">厂家往来明细表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看采购入库单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="采购组织：" size="mini">
          {{TableData.instoragList.company_name}}
        </el-form-item>
        <el-form-item label="来源单据:" size="mini">
          {{TableData.instoragList.order_sn}}
        </el-form-item>
        <el-form-item label="供应商(厂家)：" size="mini">
          {{TableData.instoragList.supplier_name}}
        </el-form-item>
        <el-form-item label="采购员：" size="mini">
          {{TableData.instoragList.buyer_name}}
        </el-form-item>
        <el-form-item label="部门：" size="mini">
          {{TableData.instoragList.department_name}}
        </el-form-item>
        <el-form-item label="厂家发货单号：" size="mini">
          {{TableData.instoragList.factorysend_sn}}
        </el-form-item>
        <el-form-item label="厂家发货日期：" size="mini">
          {{TableData.instoragList.factorysend_time}}
        </el-form-item>
        <el-form-item label="付款方式：" size="mini">
          {{TableData.instoragList.receivables_mode_name}}
        </el-form-item>
        <el-form-item label="仓库名称：" size="mini">
          {{TableData.instoragList.warehouse_name}}
        </el-form-item>
        <el-form-item label="入库类型：" size="mini">
          {{TableData.instoragList.type}}
        </el-form-item>
        <el-form-item label="业务日期：" size="mini">
          {{TableData.instoragList.business_time}}
        </el-form-item>

        <el-form-item label="单据号：" size="mini">
          {{TableData.instoragList.instorage_sn}}
        </el-form-item>
        <el-form-item label="审核状态：" size="mini">
          {{TableData.instoragList.auditor_status_name}}
        </el-form-item>
        <el-form-item label="分摊方式：" size="mini">
          {{TableData.instoragList.other_price_type_name}}
        </el-form-item>
        <el-form-item label="其他费用：" size="mini">
          {{TableData.instoragList.other_price}}
        </el-form-item>
        <el-form-item label="备注：" size="mini">
          <span>{{TableData.instoragList.remark}}</span>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goodsList" style="width: 100%" :summary-method="getSummaries" show-summary max-height="600">
        <el-table-column :show-overflow-tooltip="true" label="序号" type="index" width="60">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500px">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="production_time" label="生产日期" width="170">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="采购价" align="right">
          <template slot-scope="scope">
            {{scope.row.purchase_price|priceFormat}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="入库数量" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="subtotal_price" label="小计金额" align="right">
          <template slot-scope="scope">
            {{scope.row.subtotal_price|priceFormat}}
          </template>
        </el-table-column>
        <!--<el-table-column  :show-overflow-tooltip="true" property="warehouse_name" label="仓库1"  >-->
        <!--</el-table-column>-->
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="other_price" label="其他费用" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
        </el-table-column>
      </el-table>

      <div class="refundauditFoot">
        <span v-if="TableData.instoragList.update_user_name!=''">修改人：{{TableData.instoragList.update_user_name }}</span>
        <span v-else>
          创建人：{{TableData.instoragList.add_user_name}}
        </span>
        <span v-if="TableData.instoragList.update_time!=''">修改时间：{{TableData.instoragList.update_time}}
        </span>
        <span v-else>创建时间：{{TableData.instoragList.add_time}}
        </span>
      </div>

    </div>
    <div slot="footer" class="dialog-footer">
      <!--未审核-->
      <template v-if="TableData.instoragList.auditor_status==1">
        <el-button type="primary" @click="shenHe" v-if="authority.b_pass==1">审 核</el-button>
        <el-button @click="abolishFun" v-if="authority.b_invalid==1">作 废</el-button>
      </template>
      <template v-if="TableData.instoragList.auditor_status==2">
        <el-button @click="AUDIT_GET" v-if="authority.b_nopass==1">弃审</el-button>
      </template>
      <!--作废-->
      <template v-if="TableData.instoragList.auditor_status==2 || TableData.instoragList.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <el-button @click="goBack" v-else>取 消</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportInstorageSee",
  data() {
    return {
      authority: {},
      TableData: {
        instoragList: {},
        goodsList: []
      },
      chaid: false
    };
  },
  computed: {
    ...mapGetters(["factorydetrptInstorageSeeGet"])
  },
  methods: {
    ...mapActions(["factorydetrptIndexACT"]),
    goBack() {
      this.factorydetrptIndexACT({
        moduleName: "Index",
        params: this.factorydetrptInstorageSeeGet.params
      });
    },
    AUDIT_GET() {
      //弃审
      this.$axMethod
        .unAuditorPass({
          instorage_id: this.factorydetrptInstorageSeeGet.instorage_id
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.data,
              type: "success"
            });
            this.goBack();
          }
        });
    },
    shenHe() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.purchaseAuditorPassInStorage,
        data: {
          instorage_id: this.factorydetrptInstorageSeeGet.instorage_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    abolishFun() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.purchaseAuditorInvalidInStorage,
        data: {
          instorage_id: this.factorydetrptInstorageSeeGet.instorage_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    // 合并计算
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "合计";
          return;
        }
        const values = data.map(item => Number(item[column.property]));
        if (!values.every(value => isNaN(value))) {
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
          if (index === 6) {
            sums[index] = parseFloat(sums[index]);
            return;
          } else if (index === 7) {
            sums[index] = parseFloat(sums[index]).toFixed(2);
            return;
          } else {
            sums[index] = "";
            return;
          }
        } else {
          sums[index] = "";
        }
      });
      return sums;
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    /** 入库详情 */
    this.instorage_id = this.factorydetrptInstorageSeeGet.instorage_id;
    this.$axios({
      method: "get",
      url: this.$api.purchaseInfoInStorage,
      params: {
        instorage_id: this.instorage_id
      }
    }).then(res => {
      if (res.data.code == 200) {
        this.TableData.instoragList = res.data.data.instoragList;
        this.TableData.goodsList = res.data.data.goodsList;
      }
    });
  }
};
</script>