<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>报表系统</el-breadcrumb-item>
      <el-breadcrumb-item>财务报表</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">厂家往来明细表</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看采购退货单</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="采购组织:" label-width="100px" size="mini">
          {{TableData.info.company_name}}
        </el-form-item>
        <el-form-item label="单据号:" label-width="100px" size="mini">
          <span>{{TableData.info.refund_sn}}</span>
        </el-form-item>
        <el-form-item label="来源单号:" label-width="100px" size="mini">
          <span>{{TableData.info.instorage_sn}}</span>
        </el-form-item>
        <el-form-item label="业务日期:" label-width="100px" size="mini">
          <span>{{TableData.info.business_time}}</span>
        </el-form-item>
        <el-form-item label="出库类型:" label-width="100px" size="mini">
          <span>采购退货</span>
        </el-form-item>
        <el-form-item label="仓库名称" label-width="100px" size="mini">
          {{TableData.info.warehouse_name}}
        </el-form-item>
        <el-form-item label="供应商(厂家):" label-width="100px" size="mini">
          <span>{{TableData.info.supplier_name}}</span>
        </el-form-item>
        <el-form-item label="部门:" label-width="100px" size="mini">
          <span>{{TableData.info.department_name}}</span>
        </el-form-item>
        <el-form-item label="采购员:" label-width="100px" size="mini">
          <span>{{TableData.info.buyer_name}}</span>
        </el-form-item>
        <el-form-item label="退货原因:" label-width="100px" size="mini">
          <span>{{TableData.info.reason_name}}</span>
        </el-form-item>
        <el-form-item label="审核状态:" label-width="100px" size="mini">
          <span>{{TableData.info.auditor_status_name}}</span>
        </el-form-item>
        <el-form-item label="备注:" label-width="100px" size="mini">
          <span> {{TableData.info.remark}}</span>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_list" style="width: 100%" :summary-method="getSummaries" show-summary max-height="415">
        <el-table-column :show-overflow-tooltip="true" type="index" width="46" label="序号" align="center"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" width="80">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" width="400">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" width="88">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" :render-header="renderHeaderpro" property="production_time" width="80" label="生产日期">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="unit_name" label="单位" width="88"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" width="130" label="货位">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_num" label="数量" align="right" width="70">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="purchase_price" label="采购价" align="right" width="100">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_price" label="小计金额" align="right" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注"></el-table-column>
      </el-table>
      <div class="refundauditFoot">
        <span>创建人：{{TableData.info.add_user_name}}</span>
        <span>创建时间：{{TableData.info.add_time}}</span>
        <span>审核人：{{TableData.info.auditor_name}}</span>
        <span>审核时间：{{TableData.info.auditor_time}}</span>
        <span>修改人：{{TableData.info.update_user_name}}</span>
        <span>修改时间：{{TableData.info.update_time}}</span>
      </div>
      <div slot="footer" class="dialog-footer">
        <!--未审核-->
        <template v-if="TableData.info.auditor_status==1">
          <el-button type="primary" @click="examineFun" v-if="authority.b_pass==1">审 核</el-button>
          <el-button @click="abolishFun" v-if="authority.b_invalid==1">作 废</el-button>
        </template>
        <!--作废-->
        <template v-if="TableData.info.auditor_status==2 || TableData.info.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
          <el-button @click="goBack">返 回</el-button>
        </template>
        <template v-else>
          <el-button @click="goBack">取 消</el-button>
        </template>
        <!--<el-button type="primary" @click="examineFun" v-if="TableData.info.auditor_status_name=='未审核'">审 核</el-button>-->
        <!--<el-button @click="abolishFun" v-if="TableData.info.auditor_status_name=='未审核'">作 废</el-button>-->
        <!--<el-button @click="goBack">返 回</el-button>-->
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportRefundSee",
  data() {
    return {
      authority: {},
      printFlag: true,
      TableData: {
        info: {}
      },
      checkPage: "",
      line: null,
      hasAuditorStatusPrint: null
    };
  },
  methods: {
    ...mapActions([
      "factorydetrptIndexACT",
      "refundAdd_show",
      "refundSearch_show",
      "refundauditDefault_show"
    ]),
    renderHeaderpro() {
      return (
        <el-tooltip class="item" content="Bottom center" effect="light">
          <p slot="content">生产日期列后期将会隐藏，同时新增商品入库日期列</p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold;"
          >
            生产日期
          </span>
        </el-tooltip>
      );
    },
    AUDIT_GET() {
      this.$axMethod
        .purchaseUnAuditorPass({
          refund_id: this.refundSearch_showGet.refund_id
        })
        .then(res => {
          if (res.data.code == 200) {
            this.$message({
              message: res.data.msg,
              type: "success"
            });
            this.goBack();
          }
        });
      //弃审
    },
    examineFun() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.refundAuditorPass,
        data: {
          refund_id: this.refundSearch_showGet.refund_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.msg ? res.data.msg : res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    abolishFun() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.refundAuditorInvalid,
        data: {
          refund_id: this.refundSearch_showGet.refund_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    goBack() {
      this.factorydetrptIndexACT({
        moduleName: "Index",
        params: this.factorydetrptRefundSeeGet.params
      });
    },
    getSummaries({ columns, data }) {
      return columns.map((column, index) => {
        if (index === 0) {
          return "合计";
        }
        if (["goods_num", "goods_price"].includes(column.property)) {
          var num;
          num = this.$utils.sum(data, column.property);
          return Number.isInteger(num) ? num : num.toFixed(2);
        }
        return "";
      });
    }
  },
  computed: {
    ...mapGetters(["factorydetrptRefundSeeGet"])
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    let vm = this;
    this.$axios({
      method: "get",
      url: vm.$api.getRefundInfo,
      params: {
        refund_id: this.factorydetrptRefundSeeGet.refund_id
      }
    }).then(({ data }) => {
      if (data.code == 200) {
        console.log(data.data);
        this.$set(this, "TableData", data.data);
      }
    });
    this.checkPage = this.factorydetrptRefundSeeGet.checkPage;
  }
};
</script>


