        <el-form-item label="收货人电话：" size="mini">
          {{TableData.consignee_mobile}}
        </el-form-item>

        <el-form-item label="所属区域：" size="mini">
          {{TableData.province_name}}/{{TableData.city_name}}/{{TableData.area_name}}
        </el-form-item>

        <el-form-item label="详细地址：" size="mini">
          {{TableData.address}}
        </el-form-item>

        <el-form-item label="备注：" size="mini">
          <el-input size="small" type="text" v-model="TableData.remark" suffix-icon="el-icon-edit-outline" :maxlength="300" :disabled="true" class="boxTextarea" style="width: 200px;"></el-input>
