    <div slot="footer" class="dialog-footer">
      <!--未审核-->
      <template v-if="TableData.auditor_status==1">
        <el-button type="primary" @click="examineForm" v-if="authority.b_pass==1">审 核</el-button>
        <el-button @click="abolishForm" v-if="authority.b_invalid==1">作 废</el-button>
      </template>
      <!--作废-->
      <template v-if="TableData.auditor_status==2 || TableData.auditor_status==3 || (authority.b_invalid!=1 && authority.b_pass!=1)">
        <el-button @click="goBack">返 回</el-button>
      </template>
      <template v-else>
        <el-button @click="goBack">取 消</el-button>
      </template>
