  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportSearchCustomrebateaudit",
  data() {
    return {
      authority: {},
      formInline: {},
      TableData: {},
      checkPage: "",
      rebate_id: ""
    };
  },
  methods: {
    ...mapActions([
      "factorydetrptIndexACT",
      "salesallocationAdd",
      "salesallocationSee",
      "creditmanageAdd"
    ]),
    goBack() {
      this.factorydetrptIndexACT({
        moduleName: "Index"
      });
    },
    examineForm() {
      //审核
      this.$axios({
        method: "post",
        url: this.$api.DBauidtorPass,
        data: {
          transfer_id: this.transfer_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    abolishForm() {
      //作废
      this.$axios({
        method: "post",
        url: this.$api.DBauditorInvalid,
        data: {
          transfer_id: this.transfer_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    }
  },
  computed: {
    ...mapGetters(["factorydetrptSalesalLocationSeeGet"])
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.transfer_id = this.factorydetrptSalesalLocationSeeGet.transfer_id;
    let vm = this;
    this.$axMethod
      .DBcheckTransfer({
        transfer_id: vm.transfer_id
      })
      .then(res => {
        if (res.data.code == 200) {
          vm.TableData = res.data.data;
        }
      });
  },
  mounted() {}
};
</script>

