<template>
  <div>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
        <el-form-item label="财务组织" size="mini" :rules="rules.company_id" prop="company_id">
          <el-select v-model="params.company_id" placeholder="请选择" :filter-method="companyChange" @change="companyChange" @focus="getCompany" filterable>
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': ''">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="供应商(厂家)" size="mini" :rules="rules.supplier_id" prop="supplier_id">
          <el-select clearable v-model="params.supplier_id" maxlength="100" :filter-method="supplierChange" @focus="supplier" filterable placeholder="请选择">
            <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="审核日期" size="mini">
          <el-date-picker v-model="timeValue" @change="change" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" clearable>
          </el-date-picker>
        </el-form-item>
        <el-form-item label="关联单据号" size="mini">
          <el-input v-model="params.business_sn" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button type="primary" @click="onSearch('filterFormRef')" size="mini">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>

      </el-form>
    </div>
    <div class="add_delet">
      <el-dropdown placement="bottom-start" v-if="authority.b_export==1 && listShow">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出本供应商</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1,false)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2,false)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dropdown placement="bottom-start" v-if="authority.b_exportall==1 && listShow">
        <span split-button style="cursor:pointer;color:#4e90e2">
          <el-button plain icon="iconfont icon-daochu10" size="mini">导出全部</el-button>
        </span>
        <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
          <el-dropdown-item @click.native="exportOrder(1,true)">导出Excel文件</el-dropdown-item>
          <el-dropdown-item @click.native="exportOrder(2,true)">导出CSV逗号分隔</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="table_container" v-if="listShow">
      <el-table tooltip-effect="light" :data="TableData.tableData" style="width: 100%" :max-height="tableHeight" :highlight-current-row="true" @row-dblclick='seeF(TableData.tableData,$event)'>
        <el-table-column :show-overflow-tooltip="true" property="company_name" label="财务组织"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="department_name" label="部门"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="factory_accounts_type_name" label="单据类型"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="payable_price" label="应付款" align="right">
          <template slot-scope="scope">
            {{scope.row.payable_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="payment_price" label="付款" align="right">
          <template slot-scope="scope">
            {{scope.row.payment_price|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="balance" label="余额" align="right">
          <template slot-scope="scope">
            {{scope.row.balance|priceFormatNone}}
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="add_time" label="审核日期"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期"></el-table-column>
        <!-- <el-table-column :show-overflow-tooltip="true" property="reference_sn" label="关联单据号"></el-table-column> -->
        <el-table-column :show-overflow-tooltip="true" property="reference_sn" width="180" label="单据号">
          <template slot-scope="scope">
            <a href="javaScript: ;" @click="seeF(scope, scope.row)">
              {{scope.row.reference_sn}}
            </a>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="abstract" label="摘要"></el-table-column>
      </el-table>
      <el-row type="flex" class="row-bg">
        <el-col :span="12">
          <div class="report_row">生成时间：{{TableData.allList && TableData.allList.time}}</div>
        </el-col>
        <el-col :span="12">
          <div class="textAr report_row">制表人：{{TableData.allList && TableData.allList.name}}</div>
        </el-col>
      </el-row>
      <el-row type="flex" class="row-bg report_row">
        <el-col :span="12">
          <div>审核日期起始:{{params.start_time}}</div>
        </el-col>
        <el-col :span="12">
          <div class="textAr">审核日期截止:{{params.end_time}}</div>
        </el-col>
      </el-row>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import {
  getData,
  getInventoryFinanceCompanyOpen,
  getDepartmentListCommon,
  supplierListOpen, //获取供应商下拉
  exportFile,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "reportFactorydetrptIndex",
  data() {
    return {
      authority: {},
      params: {},
      searchParams: {},
      listShow: false,
      timeValue: [],
      supplierList: [],
      exportType: null,
      customerList: {
        options: []
      }, //客户列表
      TableData: {
        desc: {},
        tableData: []
      },
      accountype: {
        options: []
      },
      companyList: [],
      goPage: 1, //默认搜索页面
      pageSize: 30,
      loading: true,
      per_page: [30, 50, 100, 500],
      tableHeight: 600
    };
  },
  computed: {
    ...mapGetters(["factorydetrptIndexGet", "toObtainImport_Get"])
  },
  methods: {
    ...mapActions([
      "factorydetrptInstorageSeeACT",
      "factorydetrptRefundSeeACT",
      "factorydetrptOtheninstorageSeeACT",
      "factorydetrptSalesalLocationSeeACT",
      "factorydetrptPaymanageSeeACT",
      "factorydetrptthreeinstorageSeeACT",
      "toObtainImport" //导出状态
    ]),
    exportOrder(scheduleType, type) {
      //导出
      let parameter = Object.assign(this.params, {
        export_type: scheduleType
      });
      if (type) {
        this.$axMethod.exportFactoryDetailedList(parameter).then(({ data }) => {
          if (data.code == 200) {
            let num = this.toObtainImport_Get;
            this.toObtainImport(num + 1);
            this.$message({
              message: "导出操作成功，系统正在处理中，请在导出任务中下载！",
              type: "success"
            });
          }
        });
      } else {
        let arg = {
          params: parameter,
          API: "exportFactoryDetailedListUrl"
        };
        exportFile(arg);
      }
    },
    companyChange(val) {
      // 获取部门
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      this.$set(this.params, "supplier_id", "");
    },
    supplierChange(val) {
      let options = {
        company_id: this.params.company_id,
        supplier_name: val
      };
      supplierListOpen(this, options);
    },
    supplier() {
      let options = {
        company_id: this.params.company_id,
        supplier_name: null
      };
      supplierListOpen(this, options);
    },
    change(val) {
      //选择的日期
      if (Array.isArray(val)) {
        this.params.start_time = val[0];
        this.params.end_time = val[1];
      } else {
        this.params.start_time = "";
        this.params.end_time = "";
      }
    },
    onReset() {
      this.params = {};
      this.timeValue = "";
      this.params.reference_sn = "";
      this.$set(this.params, "company_id", Number(localStorage.company_id));
      this.$set(this.params, "factory_accounts_type_id", "");
      this.listShow = false;
    },
    onSearch(formName) {
      //修改或新增提交
      var vm = this;
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.params.company_id == undefined) {
            this.$message({
              message: "请先选择财务组织",
              type: "error"
            });
          } else if (this.params.supplier_id == undefined) {
            this.$message({
              message: "请先选择供应商厂家",
              type: "error"
            });
          } else {
            this.listShow = true;
            if (this.timeValue && this.timeValue.length == 2) {
              this.params.start_time = this.timeValue[0];
              this.params.end_time = this.timeValue[1];
            } else {
              this.params.start_time = this.params.end_time = "";
            }
            Object.assign(this.searchParams.params, this.params, {
              page: 1
            });
            getData(this.searchParams, 2, tableResize.bind(this));
          }
        }
      });
    },
    seeF(scope, row) {
      let referenceId = row.reference_sn.slice(-6); //后六位 单据id
      let referenceName = row.reference_sn.slice(0, 4);
      let billNum = "";
      let actionName = "";
      switch (referenceName) {
        case "CGRK":
          billNum = "instorage_id";
          name = "instorageSee";
          actionName = "factorydetrptInstorageSeeACT";
          break;
        case "CGTH":
          billNum = "refund_id";
          name = "refundSee";
          actionName = "factorydetrptRefundSeeACT";
          break;
        case "QTRK":
          billNum = "instorage_id";
          name = "otheninstorageSee";
          actionName = "factorydetrptOtheninstorageSeeACT";
          break;
        case "DBRK":
          billNum = "instorage_id";
          name = "otheninstorageSee";
          actionName = "factorydetrptOtheninstorageSeeACT";
          break;
        case "XSDB":
          billNum = "transfer_id";
          name = "salesalLocationSee";
          actionName = "factorydetrptSalesalLocationSeeACT";
          break;
        case "CJFK":
          billNum = "payment_id";
          name = "paymanageSee";
          actionName = "factorydetrptPaymanageSeeACT";
          break;
        case "TBRK":
          billNum = "instorage_id";
          name = "threeinstorageSee";
          actionName = "factorydetrptthreeinstorageSeeACT";
          break;
        default:
          break;
      }
      this[actionName]({
        moduleName: name,
        [billNum]: Number(referenceId),
        outstock_sn: row.business_sn
      });
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams, 2);
    },
    companyChange(val) {
      let options = {
        type: 2,
        company_name: val
      };
      getInventoryFinanceCompanyOpen(this, options);
      getDepartmentListCommon(this, this.params, 1);
    },
    getCompany() {
      let options = {
        type: 2,
        company_name: null
      };
      getInventoryFinanceCompanyOpen(this, options);
    },
    getDepartment() {
      getDepartmentListCommon(this, this.params); //获取部门
    },
    getAccountype() {
      this.$common.options("getFactoryAccountTypeListOption", this.accountype, {
        order_by: 1
      }); //往来类型
    }
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.companyList = [
      {
        company_id: Number(localStorage.company_id),
        company_name: localStorage.company_name
      }
    ];
    this.$set(this, "params", {
      company_id: Number(localStorage.company_id)
    });
    this.$set(this, "searchParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getFactoryDetailedList", //接口名
      vm: this //传递的this
    });

    //取消单据右侧滚动条
    tableResize.bind(this)();
    window.onresize = tableResize.bind(this);
  }
};
</script>

<style lang="less" scoped>
a {
  text-decoration: none;
}
</style>
