<template>
  <div>
    <component v-bind:is="commont"></component>
  </div>
</template>

<script>
import Index from "./factorydetrptIndex.vue";
import instorageSee from "./instorageSee.vue"; //采购入库单查看
import refundSee from "./refundSee.vue"; //采购退货单查看
import otheninstorageSee from "./otheninstorageSee.vue"; //其它入库单查看
import salesalLocationSee from "./salesallocationSee.vue"; //销售调拨单查看
import paymanageSee from "./paymanageSee.vue"; //厂家付款单查看
import threeinstorageSee from "./threeinstorageSee.vue"; //三包入库单查看
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      commont: "Index"
    };
  },
  components: {
    Index, //初始化
    instorageSee,
    refundSee,
    otheninstorageSee,
    salesalLocationSee,
    paymanageSee,
    threeinstorageSee
  },
  computed: {
    ...mapGetters([
      "factorydetrptIndexGet",
      "factorydetrptInstorageSeeGet",
      "factorydetrptRefundSeeGet",
      "factorydetrptOtheninstorageSeeGet",
      "factorydetrptSalesalLocationSeeGet",
      "factorydetrptPaymanageSeeGet",
      "factorydetrptthreeinstorageSeeGet"
    ])
  },
  watch: {
    factorydetrptIndexGet() {
      this.commont = this.factorydetrptIndexGet.moduleName;
    },
    factorydetrptInstorageSeeGet() {
      this.commont = this.factorydetrptInstorageSeeGet.moduleName;
    },
    factorydetrptRefundSeeGet() {
      this.commont = this.factorydetrptRefundSeeGet.moduleName;
    },
    factorydetrptOtheninstorageSeeGet() {
      this.commont = this.factorydetrptOtheninstorageSeeGet.moduleName;
    },
    factorydetrptSalesalLocationSeeGet() {
      this.commont = this.factorydetrptSalesalLocationSeeGet.moduleName;
    },
    factorydetrptPaymanageSeeGet() {
      this.commont = this.factorydetrptPaymanageSeeGet.moduleName;
    },
    factorydetrptthreeinstorageSeeGet() {
      this.commont = this.factorydetrptthreeinstorageSeeGet.moduleName;
    }
  }
};
</script>